body {
  background: #3a3a3a;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.header {
  padding: 20px;
  width: 100%;
  background: #202020;
  color: #c7c7c7;
}

body {
  margin: auto;
}

.item {
  float: left;
  color: #ffffff;
  width: 60%;
  padding: 10px;
  background: rgb(102, 124, 143);
  margin: 2%
}

.item p,
.item b,
.item h3 {
  margin: 1%;
}


main .item__btn {
  float: right;
  margin-right: 0.625em;
  color: rgb(30, 59, 85);
  cursor: pointer;
  transform: scale(1.3);
}

aside {
  margin: 2%;
}

aside form input:not([type="checkbox"]),
aside form textarea,
aside form button {
  width: 27%;
  outline: none;
  border: 0;
  background: #202020;
  padding: 2%;
  margin-bottom: 1%;
}

aside form button {
  width: 30%;
}


@media (hover:hover) {
  main .item__btn:hover {
    opacity: 0.5;
  }
}

@media screen and (max-width:500px) {
  .item {
    width: 90%;
  }

  aside form input:not([type="checkbox"]),
  aside form textarea {
    width: 94%;
  }

  aside form button {
    width: 98%;
  }
}